import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ByshiftComponent } from './byshift.component';
import { AddComponent } from './add/add.component';
import { DetailComponent } from './detail/detail.component';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { SelectWorkScheduleComponent } from './select-work-schedule/select-work-schedule.component';
import { SelectOverTimeComponent } from './select-over-time/select-over-time.component';

const routes: Routes = [
  {
    path: '',
    component: ByshiftComponent
  },
  {
    path: 'add',
    component: AddComponent
  },
  {
    path: 'detail',
    component: DetailComponent
  },
]

@NgModule({
  declarations: [ByshiftComponent, AddComponent, DetailComponent, SelectWorkScheduleComponent, SelectOverTimeComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule.withConfig({ warnOnNgModelWithFormControl: 'never' }),
    TranslateModule
  ]
})
export class ByshiftModule { }
