import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-select-over-time',
  templateUrl: './select-over-time.component.html',
  styleUrls: ['./select-over-time.component.scss']
})
export class SelectOverTimeComponent implements OnInit {
  @Input('visibleOTCoefficent') visibleOTCoefficent: boolean = false;
  @Output('closeAndReceiveOTCoefficent') closeAndReceiveOTCoefficent = new EventEmitter<any>()
  @Input('coefficient') coefficient;
  formOT: FormGroup
  constructor() { }

  ngOnInit(): void {
    this.initForm()
  }
  initForm() {
    this.formOT = new FormGroup({
      bWork: new FormControl(this.coefficient.bWork, [Validators.required]),
      aWork: new FormControl(this.coefficient.aWork, [Validators.required]),
      coefficient: new FormControl(this.coefficient.coefficient, [Validators.required]),
      block: new FormControl(this.coefficient.block, [Validators.required]),
    })
  }
  goBack() {
    this.closeAndReceiveOTCoefficent.emit(this.formOT.value)
  }
}
