import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-select-work-schedule',
  templateUrl: './select-work-schedule.component.html',
  styleUrls: ['./select-work-schedule.component.scss']
})
export class SelectWorkScheduleComponent implements OnInit {
  @Input('visibleWorktime') visibleWorktime: boolean = false;
  @Output('closeAndReceiveWorkTime') closeAndReceiveWorkTime = new EventEmitter<any>()
  @Input('worktime') worktime
  constructor() { }

  ngOnInit(): void {
  }
  updateStatus(data, event) {
    data.checked = event;
  }
  goBack() {
    this.closeAndReceiveWorkTime.emit(this.worktime)
  }

}
